  width: 100%;
  padding: 0 2rem;
}
header .visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
header .visually-hidden.focusable:focus {
  clip: auto;
  clip-path: none;
  height: auto;
  overflow: auto;
  position: static;
  white-space: normal;
  width: auto;
}
header nav {
  background: transparent;
  width: 100%;
  padding: 1.5rem 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  column-gap: 4rem;
}
@media (prefers-reduced-motion: no-preference) {
  header nav .menu--hamburger * {
    transition: all 0.5s ease-out;
  }
}
header nav .menu--hamburger {
  display: block;
  cursor: pointer;
  background: none;
  border: none;
  justify-self: flex-end;
}
header nav.open .menu--hamburger .top-bun {
  opacity: 0;
}
header nav.open .menu--hamburger .patty {
  transform: rotate(45deg) translate(20%, -48%);
}
header nav.open .menu--hamburger .bottom-bun {
  transform: rotate(-45deg) translate(-48%, -20%);
}
header nav.open .nav--links {
  display: flex;
}
header nav .logo {
  max-width: 160px;
  display: flex;
  align-items: center;
}
header nav .logo img {
  object-fit: cover;
  min-width: 143px;
  min-height: 28px;
}
header nav .nav--links {
  width: 100%;
  display: none;
  flex-direction: column;
  align-items: flex-start;
  padding: 1.5rem 0;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid hsl(249, 14%, 27%);
}
header nav .nav--links > a {
  cursor: pointer;
  text-decoration: none;
  width: fit-content;
  margin: 0.5rem auto 0.5rem 0;
  color: hsl(0, 0%, 100%);
  display: block;
  word-break: normal;
}
header nav .nav--links > a:hover {
  color: #9385ff;
}
header nav .nav--links .nav--buttons .nav--login--mobile .c4dropdown--button {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.75rem;
  color: #7f63ef;
  justify-content: flex-start;
  background: transparent;
  border: 0px;
  padding: 0px;
  margin-bottom: 0.5rem;
  font-family: "Satoshi Variable", sans-serif;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: normal;
}
@media (prefers-reduced-motion: no-preference) {
  header nav .nav--links .nav--buttons .nav--login--mobile .c4dropdown--button {
    transition: color 0.2s ease-in-out;
  }
}
header nav .nav--links .nav--buttons .nav--login--mobile .c4dropdown--button:hover {
  color: #9385ff;
}
@media (min-width: 960px) {
  header nav .nav--links .nav--buttons .nav--login--mobile {
    display: none;
  }
}
header nav .nav--links .nav--buttons .nav--dropdown--login {
  display: none;
}
@media (min-width: 960px) {
  header nav .nav--links .nav--buttons .nav--dropdown--login {
    display: block;
    background: transparent;
    border: 2px solid #7549ff;
    border-radius: 0.2rem;
    padding: 0.25rem 0.5rem;
  }
  header nav .nav--links .nav--buttons .nav--dropdown--login:hover {
    background: #7549ff;
  }
  header nav .nav--links .nav--buttons .nav--dropdown--login--trigger {
    font-family: "Satoshi Variable", sans-serif;
    font-weight: 500 !important;
    text-decoration: none !important;
    text-align: center;
    background: transparent;
    color: hsl(0, 0%, 100%);
    padding: 0;
  }
  header nav .nav--links .nav--buttons .nav--dropdown--login .c4dropdown--button {
    cursor: pointer;
  }
}
@media (min-width: 960px) {
  header nav {
    flex-wrap: nowrap;
  }
  header nav .menu--hamburger {
    display: none;
    height: 25px;
  }
  header nav .nav--links {
    display: flex;
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: none;
    padding: 0;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 1rem;
  }
  header nav .nav--links > a {
    width: auto;
  }
